//SEARCH
.search {
    flex: 0 0 40%;

    display: flex;
    align-items: center;
    justify-content: center;

    &__input {
        font-family: inherit;
        font-size: inherit;
        color: inherit;
        background-color: $color-grey-light-2;
        border: none;
        padding: 0.7rem 2rem;
        border-radius: 50px;
        width: 90%;
        transition: all 0.2s;
        margin-right: -3.5rem;

        &:focus {
            outline: none;
            width: 100%;
            background-color: $color-grey-light-3;
        }

        &::-webkit-input-placeholder{
            font-weight: 300;
            color: $color-grey-light-4;
        }
    }

    &__input:focus + &__button {
        background-color: $color-grey-light-3;
    }

    

    &__button {
        height: 2rem;
        border: none;
        background-color: $color-grey-light-2;

        &:focus {
            outline: none;
        }

        &:active {
            transform: translateY(2px);
        }
    }

    &__icon {
        height: 2rem;
        width: 2rem;
        fill: $color-grey-dark-3;
    }
}
